.header {
  &__nav-main {
    min-height: 56px;
  }

  &__wrapper {
    position: relative;

    @media (min-width: $tablet-width) {
      width: 768px;
      margin: 0 auto;
    }

    @media (min-width: $desktop-width) {
      width: 1200px;
    }
  }

  &__toggle-button {
    width: 23px;
    height: 22px;
    border: none;
    background-color: transparent;
    position: relative;
    z-index: 1;

    &--close::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 23px;
      height: 4px;
      background-color: $light-blue;
      box-shadow: 0 9px 0 0 $light-blue, 0 18px 0 0 $light-blue;
    }

    &:hover {
      opacity: 0.6;
    }

    &:active {
      opacity: 0.3;
    }

    &--open::before,
    &--open::after {
      content: "";
      position: absolute;
      top: 10px;
      left: 0;
      width: 22px;
      border-radius: 1px;
      height: 4px;
      background-color: #ffffff;
      transform: rotate(45deg);
    }

    &--open::after {
      transform: rotate(-45deg);
    }

    @media (min-width: $tablet-width) {
      display: none;
    }
  }

  &__nav-wrapper {
    display: flex;
    justify-content: flex-end;
    padding: 17px 20px;

    @media (min-width: $tablet-width) {
      padding: 0;
      display: block;
    }
  }

  &__nav-list {
    position: absolute;
    top: 0;
    align-items: center;
    list-style-type: none;
    width: 100%;
    display: none;

    &--open {
      display: block;
    }

    @media (min-width: $tablet-width) {
      display: grid;
      grid-template-columns: 156px 1fr 1fr 156px;
      position: relative;
    }

    @media (min-width: $desktop-width) {
      grid-template-columns: 1fr 1fr 1fr 1fr;
    }
  }

  &__nav-item {
    background-color: $light-blue;
    border-bottom: 1px solid rgba(1, 1, 1, 0.2);
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2);
    position: relative;

    &--gradient:last-child::before {
      position: absolute;
      content: "";
      width: 100%;
      height: 7px;
      bottom: 0;
      background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);

      @media (min-width: $tablet-width) {
        content: none;
      }
    }

    @media (min-width: $tablet-width) {
      position: static;
      box-shadow: none;
      border-bottom: none;
      background-color: transparent;
      padding: 15px 0 15px 0;

      &:nth-child(1),
      &:nth-child(4n+1) {
        padding-left: 49px;
      }

      &:nth-child(2),
      &:nth-child(4n+2) {
        padding-left: 26px;
      }

      &:nth-child(3),
      &:nth-child(4n+3) {
        padding-right: 19px;
        text-align: right;
      }

      &:nth-child(4),
      &:nth-child(4n+4) {
        padding-left: 32px;
      }
    }

    @media (min-width: $desktop-width) {
      &:nth-child(1),
      &:nth-child(4n+1) {
        padding-left: 90px;
      }

      &:nth-child(2),
      &:nth-child(4n+2) {
        padding-left: 9px;
      }

      &:nth-child(3),
      &:nth-child(4n+3) {
        padding-right: 7px;
      }

      &:nth-child(4),
      &:nth-child(4n+4) {
        padding-left: 132px;
      }
    }
  }

  &__nav-link {
    width: 100%;
    display: inline-block;
    text-align: center;
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 40px;
    text-transform: uppercase;
    color: #ffffff;
    text-decoration: none;
    padding: 10px 0 5px 0;

    @media (min-width: $tablet-width) {
      font-weight: 700;
      font-size: 14px;
      line-height: 26px;
      color: $black;
      display: inline;
      width: auto;
      padding: 0;
    }

    &:hover {
      color: $light-blue;

      @media (min-width: $mobile-width) {
        background-color: $medium-dark-blue;
        color: #ffffff;
      }
    }

    &:active {
      color: rgba(0, 0, 0, 0.3);

      @media (min-width: $mobile-width) {
        color: rgba(255, 255, 255, 0.3);
        background-color: $dark-blue;
      }
    }

    &--current {
      color: #766357;
    }
  }
}

