.page-footer {
  &__link-contacts {
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 21px;
    line-height: 26px;
    text-transform: uppercase;
    color: $black;
    text-decoration: none;
  }

  &__contacts {
    text-align: center;
    margin-bottom: 33px;

    @media (min-width: $tablet-width) {
      margin-bottom: 0;
    }

    @media (min-width: $desktop-width) {
      max-width: 180px;
    }
  }

  &__social-link {
    width: 46px;
    height: 48px;
    background-color: $light-blue;
    background-repeat: no-repeat;
    background-position: center;
    display: block;

    &--twitter {
      background-image: url("../image/icons/icon-twitter.svg");
    }

    &--facebook {
      background-image: url("../image/icons/icon-facebook.svg");
    }

    &--youtube {
      background-image: url("../image/icons/icon-youtube.svg");
    }

    &:hover {
      background-color: $medium-dark-blue;
    }

    &:active {
      background-color: $dark-blue;
    }
  }

  &__social-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    list-style-type: none;
    max-width: 151px;
    margin: 0 auto;
    gap: 10px 5px;
    margin-bottom: 40px;

    @media (min-width: $tablet-width) {
      margin-bottom: 0;
    }

    @media (min-width: $desktop-width) {
      max-width: 180px;
    }
  }

  &__copyright {
    display: flex;
    align-items: center;
    justify-content: center;

    @media (min-width: $desktop-width) {
      max-width: 160px;
    }
  }

  &__wrapper {
    padding: 39px 0 23px 0;

    @media (min-width: $tablet-width) {
      display: flex;
      align-items: center;
      padding: 38px 59px 36px 59px;
      width: 768px;
      margin: 0 auto;
    }

    @media (min-width: $desktop-width) {
      width: 1200px;
      padding: 36px 120px 36px 120px;
    }
  }

  &__copyright-text {
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 26px;
    text-transform: uppercase;
    color: $black;
    margin-right: 12px;
  }

  &__copyright-icon {
    fill: #000000;

    &:hover {
      fill: $light-blue;
    }

    &:active {
      fill: rgba(0, 0, 0, 0.3);
    }
  }
}
