.video {
  &__heding {
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 26px;
    text-align: center;
    text-transform: uppercase;
    color: $black;
    margin-bottom: 28px;

    @media (min-width: $tablet-width) {
      font-size: 30px;
      margin-bottom: 31px;
    }
  }

  &__text {
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    text-align: center;
    text-transform: uppercase;
    color: $dark-grey;
  }

  &__header {
    width: 320px;
    margin: 0 auto;
    padding: 35px 26px 33px 26px;

    @media (min-width: $tablet-width) {
      width: 450px;
      padding: 58px 0 80px 0;
    }
  }

  &__wrapper {
    width: 320px;
    margin: 0 auto;
    position: relative;
    padding-bottom: 43px;

    @media (min-width: $tablet-width) {
      width: 768px;
      padding: 0 60px;
    }

    @media (min-width: $desktop-width) {
      width: 800px;
      padding: 0;
    }
  }

  &__button-replay {
    width: 25px;
    height: 25px;
    border: none;
    background-color: transparent;
    padding-left: 25px;
    padding-bottom: 27px;
  }

  &__button-subtitles {
    width: 36px;
    height: 30px;
    border: none;
    background-color: transparent;
    margin-right: 31px;
    padding-bottom: 5px;

    @media (min-width: $tablet-width) {
      margin-right: 24px;
      padding-bottom: 1px;
    }
  }

  &__button-fullscreen {
    width: 30px;
    height: 30px;
    border: none;
    background-color: transparent;
    padding-right: 13px;
    padding-bottom: 1px;

    @media (min-width: $tablet-width) {
      padding: 0;
      margin-right: 26px;
    }
  }

  &__bar::-webkit-slider-thumb {
    appearance: none;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    cursor: pointer;
    background-color: #ffffff;
  }

  &__bar::-moz-range-thumb {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    cursor: pointer;
    background-color: #ffffff;
  }

  &__bar {
    appearance: none;
    width: 235px;
    height: 3px;
    border-radius: 5px;
    background-color: rgba(255, 255, 255, 0.2);
    outline: none;

    @media (min-width: $tablet-width) {
      width: 389px;
    }

    @media (min-width: $desktop-width) {
      width: 540px;
    }
  }

  &__wrapper-bar {
    background-color: rgba(0, 0, 0, 0.6);
    margin: 0 auto;
    padding: 12px 23px 15px 22px;
    grid-row: 1;
    grid-column: 1/-1;
    position: absolute;
    top: 156px;

    @media (min-width: $tablet-width) {
      position: static;
      background-color: transparent;
      padding: 12px 4px 21px 22px;
    }
  }

  &__control-panel {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 18px 31px 14px 20px;
    background-color: #f2f2f2;

    @media (min-width: $tablet-width) {
      display: flex;
      background-color: rgba(0, 0, 0, 0.6);
      align-items: center;
      position: absolute;
      bottom: 0;
      width: 648px;
      padding: 22px 10px 20px 10px;
    }

    @media (min-width: $desktop-width) {
      width: 800px;
    }
  }

  &__wrapper-control {
    display: flex;
    justify-content: flex-end;
  }

  &__replay-icon,
  &__subtitles-icon,
  &__fullscreen-icon {
    fill: #5c6870;

    @media (min-width: $tablet-width) {
      fill: #ffffff;
    }
  }

  &__content {
    padding-bottom: 0;
  }

  &__video-image {
    display: block;
    object-fit: contain;
    max-width: 100%;
    height: auto;
  }
}
