.form {
  &__title {
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 26px;
    text-align: center;
    text-transform: uppercase;
    color: $black;
    left: 50%;
    transform: translateX(-50%);

    @media (min-width: $tablet-width) {
      transform: none;
      left: 0;
    }

    &--full-name {
      position: absolute;
      top: 18px;

      @media (min-width: $tablet-width) {
        text-align: left;
        top: 17px;
      }

      @media (min-width: $desktop-width) {
        top: 2px;
        left: 0;
      }
    }

    &--contact-information {
      position: absolute;
      top: 20px;
      width: 100%;

      @media (min-width: $tablet-width) {
        position: static;
        width: auto;
        background-color: #ffffff;
        padding: 0 63px;
      }
    }

    &--radio-button {
      position: absolute;
      top: 16px;
      width: 100%;

      @media (min-width: $tablet-width) {
        text-align: left;
      }

      @media (min-width: $desktop-width) {
        text-align: right;
        top: 2px;
        left: 8px;
      }
    }

    &--checkbox {
      position: absolute;
      top: 25px;

      @media (min-width: $tablet-width) {
        position: static;
        background-color: #ffffff;
        padding: 0 63px;
        max-width: 400px;
      }
    }

    &--text-review {
      position: absolute;
      top: 29px;
      width: 100%;

      @media (min-width: $tablet-width) {
        position: static;
        background-color: #ffffff;
        width: auto;
        padding: 0 76px;
      }
    }

    @media (min-width: $tablet-width) {
      font-size: 21px;
    }
  }

  &__label {
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 26px;
    text-transform: uppercase;
    color: $dark-grey;
    margin-right: 13px;

    &--second {
      margin-right: 0;
      margin-left: 22px;
      display: inline-block;
      margin-bottom: 12px;

      @media (min-width: $desktop-width) {
        margin: 0;
        margin-right: 16px;
      }
    }
  }

  &__field {
    border: none;
    position: relative;

    &--name-border::after {
      content: "";
      position: absolute;
      width: 100%;
      height: 1px;
      background-color: #e5e5e5;
      top: -2px;
      left: 0;
      right: 0;
      bottom: 0;
    }

    &--radio-border::after {
      content: "";
      position: absolute;
      width: 100%;
      height: 1px;
      background-color: #e5e5e5;
      top: -4px;
      left: 0;
      right: 0;
      bottom: 0;
    }

    &--ckeckbox-border::after,
    &--review-border::after {
      content: "";
      position: absolute;
      width: 100%;
      height: 1px;
      background-color: #e5e5e5;
      top: 9px;
      left: 0;
      right: 0;
      bottom: 0;
    }

    @media (min-width: $tablet-width) {
      &--name-border::after,
      &--radio-border::after,
      &--ckeckbox-border::after,
      &--review-border::after {
        content: none;
      }
    }

    &--full-name {
      padding: 65px 20px 25px 20px;
      order: 1;

      @media (min-width: $tablet-width) {
        width: 266px;
        margin-left: auto;
        padding: 99px 0 70px 0;
      }

      @media (min-width: $desktop-width) {
        width: 400px;
        padding: 84px 0 70px 0;
      }
    }

    &--radio-button {
      order: 3;
      padding: 79px 20px 31px 20px;

      @media (min-width: $tablet-width) {
        order: 0;
        width: 312px;
        padding: 113px 0 20px 0;
      }

      @media (min-width: $desktop-width) {
        width: 344px;
        padding: 98px 0 20px 4px;
      }
    }

    &--contact-information {
      order: 2;
      padding: 62px 20px 28px 20px;

      @media (min-width: $tablet-width) {
        grid-column: 1/-1;
        border-top: 1px solid #dbdbdb;
        padding: 20px 0 49px 0;
      }

      @media (min-width: $desktop-width) {
        display: grid;
        grid-template-columns: 400px 1fr;
        column-gap: 79px;
        padding: 73px 0 49px 0;
      }
    }

    &--ckeckbox {
      order: 4;
      padding: 113px 20px 30px 20px;

      @media (min-width: $tablet-width) {
        grid-column: 1/-1;
        border-top: 1px solid #dbdbdb;
        padding: 56px 0 30px 0;
      }

      @media (min-width: $desktop-width) {
        padding: 58px 0 30px 0;
      }
    }

    &--text-review {
      padding: 71px 20px 47px 20px;
      order: 5;

      @media (min-width: $tablet-width) {
        grid-column: 1/-1;
        border-top: 1px solid #dbdbdb;
        padding: 57px 0 54px 0;
      }

      @media (min-width: $desktop-width) {
        padding: 57px 0 51px 0;
      }
    }

    &--border-left::before {
      content: none;

      @media (min-width: $tablet-width) {
        content: "";
        position: absolute;
        width: 1px;
        height: 292px;
        background-color: #dbdbdb;
        left: -59px;
        top: 19px;
      }

      @media (min-width: $desktop-width) {
        left: -80px;
        top: 3px;
      }
    }
  }

  &__wrapper {
    display: flex;
    flex-direction: column;

    @media (min-width: $tablet-width) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      padding: 0 60px;
      width: 768px;
      margin: 0 auto;
    }

    @media (min-width: $desktop-width) {
      width: 1200px;
      padding: 0 120px;
    }
  }

  &__group {
    display: flex;
    align-items: center;
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__input {
    width: 100%;
    padding: 21.5px 22px;
    border: none;
    background-color: #f2f2f2;

    &::placeholder {
      font-family: "PT Sans", sans-serif;
      font-style: normal;
      font-weight: 700;
      font-size: 18px;
      line-height: 26px;
      color: $black;
    }

    &:hover {
      background-color: #e5e5e5;
    }

    &:active,
    &:focus {
      background-color: #ffffff;
      outline: 4px solid #e5e5e5;
    }
  }

  &__submit {
    order: 6;
    margin-bottom: 38px;
    padding: 0 20px;

    @media (min-width: $tablet-width) {
      grid-column: 1/-1;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      align-items: center;
      padding: 0;
      margin-bottom: 61px;
    }

    @media (min-width: $desktop-width) {
      margin-bottom: 67px;
    }
  }

  &__group-second {
    margin-bottom: 8px;
    position: relative;

    @media (min-width: $tablet-width) {
      margin-bottom: 18px;
    }

    &:last-child {
      margin-bottom: 0;
    }

    &--telephone::before {
      position: absolute;
      content: "";
      width: 58px;
      height: 58px;
      background-color: #e6e6e6;
      background-image: url("../image/icons/phone-form.svg");
      background-repeat: no-repeat;
      background-position: center;
      right: 0;
      top: 38px;

      @media (min-width: $desktop-width) {
        top: 0;
      }
    }

    &--mail::before {
      position: absolute;
      content: "";
      width: 58px;
      height: 58px;
      background-color: #e6e6e6;
      background-image: url("../image/icons/email-form.svg");
      background-repeat: no-repeat;
      background-position: center;
      right: 0;
      top: 38px;

      @media (min-width: $desktop-width) {
        top: 0;
      }
    }

    @media (min-width: $desktop-width) {
      display: flex;
      align-items: center;
      margin-bottom: 0;

      &--mail {
        display: grid;
        grid-template-columns: 163px 1fr;
      }
    }
  }

  &__tag {
    position: relative;
    padding-left: 78px;
    display: flex;
    align-items: center;

    @media (min-width: $tablet-width) {
      padding-left: 75px;
    }
  }

  &__control {
    width: 54px;
    height: 57px;
    position: absolute;
    background-color: #f2f2f2;
    left: 0;

    &:hover {
      background-color: #e5e5e5;
    }

    &:active {
      background-color: #d5d5d5;
    }
  }

  &__list {
    list-style-type: none;
  }

  &__input-check[type="checkbox"]:checked + &__control {
    background-image: url("../image/icons/checkbox__checked.svg");
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 5px;
  }

  &__input-control[type="radio"] + &__control {
    border-radius: 50%;
  }

  &__input-control[type="radio"]:checked + &__control::before {
    position: absolute;
    top: 19px;
    left: 18px;
    width: 20px;
    height: 20px;
    background-color: #242424;
    border-radius: 50%;
    content: "";
  }

  &__check-name {
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 26px;
    color: $black;
  }

  &__item {
    margin-bottom: 44px;

    @media (min-width: $tablet-width) {
      margin-bottom: 53px;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__list-check {
    list-style-type: none;

    @media (min-width: $tablet-width) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      max-width: 484px;
      margin: 0 auto;
      gap: 9px 34px;
    }

    @media (min-width: $desktop-width) {
      grid-template-columns: repeat(4, 1fr);
      max-width: none;
      justify-content: center;
      column-gap: 53px;
    }
  }

  &__item-check {
    margin-bottom: 54px;

    @media (min-width: $tablet-width) {
      padding-left: 14px;
    }

    @media (min-width: $desktop-width) {
      padding-left: 0;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__label-check {
    padding-left: 79px;
    display: flex;
    align-items: center;
    position: relative;
  }

  &__submit-description {
    display: none;

    @media (min-width: $tablet-width) {
      display: block;
      text-align: right;
      font-family: "PT Sans", sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 26px;
      text-transform: uppercase;
      color: $dark-grey;
      padding-top: 7px;
    }
  }

  &__large-text {
    width: 100%;
    min-height: 160px;
    background-color: #f2f2f2;
    border: none;

    &::placeholder {
      font-family: "PT Sans", sans-serif;
      font-style: normal;
      font-weight: 700;
      font-size: 18px;
      line-height: 26px;
      color: $black;
      padding: 13px 23px 29px 21px;

      @media (min-width: $tablet-width) {
        padding: 18px 21px 24px;
      }

      @media (min-width: $desktop-width) {
        padding: 16px 21px 24px;
      }
    }
  }

  &__submit-button {
    width: 100%;
    background-color: $light-blue;
    border: none;
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 21px;
    line-height: 26px;
    text-transform: uppercase;
    color: #ffffff;
    padding: 19px 0 13px 0;

    @media (min-width: $tablet-width) {
      grid-column: 1/2;
    }
  }

  &__label-second {
    display: none;

    @media (min-width: $desktop-width) {
      display: block;
    }
  }

  &__label-first {
    @media (min-width: $desktop-width) {
      display: none;
    }
  }
}
