.advantage {
  &__title {
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 26px;
    text-transform: uppercase;
    color: #000000;
    margin-bottom: 32px;

    @media (min-width: $tablet-width) {
      font-size: 21px;
      line-height: 26px;
    }
  }

  &__text {
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 26px;
    text-transform: uppercase;
    color: #333333;
  }

  &__header {
    padding: 32px 17px 27px 18px;
    margin: 0 auto;
    text-align: center;
    width: 298px;

    @media (min-width: $tablet-width) {
      width: 530px;
      padding: 43px 17px 53px 18px;
    }

    @media (min-width: $desktop-width) {
      padding: 55px 17px 41px 18px;
    }
  }
}
