.modal-success {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: none;

  &__wrapper {
    width: 320px;
    background-color: #ffffff;
    box-shadow: 0 7px 15px rgba(0, 1, 1, 0.15);
    border-radius: 1px;
    position: relative;
    margin: auto;

    @media (min-width: $tablet-width) {
      width: 640px;
    }
  }

  &__content {
    padding: 31px 39px 21px 37px;

    @media (min-width: $tablet-width) {
      padding: 49px 39px 26px 37px;
    }
  }

  &--open {
    display: flex;
  }

  &__title {
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 26px;
    text-align: center;
    text-transform: uppercase;
    color: $black;
    margin-bottom: 30px;

    @media (min-width: $tablet-width) {
      font-size: 30px;
    }
  }

  &__text {
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    text-align: center;
    text-transform: uppercase;
    color: $dark-grey;
  }

  &__button-wrapper {
    width: 100%;
    background-color: #f2f2f2;
    padding: 20px 0;

    @media (min-width: $tablet-width) {
      padding: 44px 0;
    }
  }

  &__button {
    display: block;
    margin: 0 auto;
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 26px;
    text-transform: uppercase;
    color: #ffffff;
    border: none;
    background-color: $light-blue;
    padding: 18px 0 14px 0;
    min-width: 280px;

    @media (min-width: $tablet-width) {
      min-width: 458px;
    }
  }
}
