.gallery {
  &__header {
    width: 320px;
    margin: 0 auto;
    padding: 24px 20px 35px 20px;

    @media (min-width: $tablet-width) {
      width: 440px;
      padding: 64px 0 77px 0;
    }

    @media (min-width: $desktop-width) {
      padding: 98px 0 79px 0;
    }
  }

  &__title {
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 26px;
    text-align: center;
    text-transform: uppercase;
    color: $black;
    margin-bottom: 30px;

    @media (min-width: $tablet-width) {
      font-size: 30px;
      margin-bottom: 33px;
    }
  }

  &__text {
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    text-align: center;
    text-transform: uppercase;
    color: $dark-grey;
  }

  &__item-photo {
    padding: 24px 10px 15px 10px;
    border-top: 1px solid #e5e5e5;

    @media (min-width: $tablet-width) {
      border-top: none;
      padding: 0;
    }

    &--main-photo {
      grid-column: 1/-1;
    }

    &:last-child {
      border-bottom: 1px solid #e5e5e5;
    }
  }

  &__wrapper-photo {
    width: 280px;
    margin: 0 auto;
    position: relative;

    @media (min-width: $tablet-width) {
      width: auto;
    }
  }

  &__title-photo {
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 24px;
    align-items: center;
    text-transform: uppercase;
    color: $black;

    @media (min-width: $desktop-width) {
      color: #ffffff;
    }
  }

  &__image {
    display: block;
    object-fit: contain;
    max-width: 100%;
    height: auto;
  }

  &__description-photo {
    margin-top: 11px;

    @media (min-width: $tablet-width) {
      margin-top: 0;
      display: flex;
      justify-content: space-between;
      background-color: #f2f2f2;
      padding: 16px 20px 16px 20px;
    }

    @media (min-width: $desktop-width) {
      position: absolute;
      z-index: 1;
      bottom: 0;
      right: 0;
      left: 0;
      background-color: rgba(0, 0, 0, 0.3);
    }
  }

  &__author {
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: $dark-grey;

    @media (min-width: $desktop-width) {
      color: #ffffff;
    }
  }

  &__count-like {
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 24px;
    align-items: center;
    text-transform: uppercase;
    color: $black;
    padding-top: 3px;

    @media (min-width: $desktop-width) {
      color: #ffffff;
    }
  }

  &__button-like {
    width: 23px;
    height: 21px;
    border: none;
    background-image: url("../image/icons/like.svg");
    background-color: transparent;
    margin-right: 14px;

    @media (min-width: $desktop-width) {
      background-image: url("../image/icons/like-desktop.svg");
    }

    &--main-like {
      @media (min-width: $tablet-width) {
        margin-right: 9px;
        margin-bottom: 2px;
      }
    }
  }

  &__wrapper-likes {
    display: flex;
    align-items: center;

    @media (min-width: $tablet-width) {
      margin-right: 11px;
    }

    @media (min-width: $desktop-width) {
      margin-right: 0;
    }
  }

  &__wrapper-author {
    margin-bottom: 9px;

    @media (min-width: $tablet-width) {
      margin-bottom: 0;
    }
  }

  &__list-photo {
    list-style-type: none;

    @media (min-width: $tablet-width) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      padding: 0 60px;
      width: 768px;
      margin: 0 auto;
    }

    @media (min-width: $desktop-width) {
      width: 1200px;
      padding: 0;
      grid-template-columns: repeat(3, 1fr);
    }
  }
}
