.logo {
  &__image-teblet {
    display: none;
  }

  &__image-desktop {
    display: none;
  }

  @media (min-width: $tablet-width) {
    &__image-mobile {
      display: none;
    }

    &__image-teblet {
      display: block;
    }

    &__image-desktop {
      display: none;
    }
  }

  @media (min-width: $desktop-width) {
    &__image-teblet {
      display: none;
    }

    &__image-desktop {
      display: block;
    }
  }

  position: absolute;
  left: 50%;
  top: 0;
  transform: translateX(-50%);
}
