@font-face {
  font-family: "PT Sans";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/PTSans-Regular.ttf") format("truetype");
}
@font-face {
  font-family: "PT Sans";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/PTSans-Bold.ttf") format("truetype");
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.header__nav-main {
  min-height: 56px;
}
.header__wrapper {
  position: relative;
}
@media (min-width: 768px) {
  .header__wrapper {
    width: 768px;
    margin: 0 auto;
  }
}
@media (min-width: 1200px) {
  .header__wrapper {
    width: 1200px;
  }
}
.header__toggle-button {
  width: 23px;
  height: 22px;
  border: none;
  background-color: transparent;
  position: relative;
  z-index: 1;
}
.header__toggle-button--close::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 9px 0 0 #81b3d2, 0 18px 0 0 #81b3d2;
}
.header__toggle-button:hover {
  opacity: 0.6;
}
.header__toggle-button:active {
  opacity: 0.3;
}
.header__toggle-button--open::before, .header__toggle-button--open::after {
  content: "";
  position: absolute;
  top: 10px;
  left: 0;
  width: 22px;
  border-radius: 1px;
  height: 4px;
  background-color: #ffffff;
  transform: rotate(45deg);
}
.header__toggle-button--open::after {
  transform: rotate(-45deg);
}
@media (min-width: 768px) {
  .header__toggle-button {
    display: none;
  }
}
.header__nav-wrapper {
  display: flex;
  justify-content: flex-end;
  padding: 17px 20px;
}
@media (min-width: 768px) {
  .header__nav-wrapper {
    padding: 0;
    display: block;
  }
}
.header__nav-list {
  position: absolute;
  top: 0;
  align-items: center;
  list-style-type: none;
  width: 100%;
  display: none;
}
.header__nav-list--open {
  display: block;
}
@media (min-width: 768px) {
  .header__nav-list {
    display: grid;
    grid-template-columns: 156px 1fr 1fr 156px;
    position: relative;
  }
}
@media (min-width: 1200px) {
  .header__nav-list {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}
.header__nav-item {
  background-color: #81b3d2;
  border-bottom: 1px solid rgba(1, 1, 1, 0.2);
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2);
  position: relative;
}
.header__nav-item--gradient:last-child::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 7px;
  bottom: 0;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}
@media (min-width: 768px) {
  .header__nav-item--gradient:last-child::before {
    content: none;
  }
}
@media (min-width: 768px) {
  .header__nav-item {
    position: static;
    box-shadow: none;
    border-bottom: none;
    background-color: transparent;
    padding: 15px 0 15px 0;
  }
  .header__nav-item:nth-child(1), .header__nav-item:nth-child(4n+1) {
    padding-left: 49px;
  }
  .header__nav-item:nth-child(2), .header__nav-item:nth-child(4n+2) {
    padding-left: 26px;
  }
  .header__nav-item:nth-child(3), .header__nav-item:nth-child(4n+3) {
    padding-right: 19px;
    text-align: right;
  }
  .header__nav-item:nth-child(4), .header__nav-item:nth-child(4n+4) {
    padding-left: 32px;
  }
}
@media (min-width: 1200px) {
  .header__nav-item:nth-child(1), .header__nav-item:nth-child(4n+1) {
    padding-left: 90px;
  }
  .header__nav-item:nth-child(2), .header__nav-item:nth-child(4n+2) {
    padding-left: 9px;
  }
  .header__nav-item:nth-child(3), .header__nav-item:nth-child(4n+3) {
    padding-right: 7px;
  }
  .header__nav-item:nth-child(4), .header__nav-item:nth-child(4n+4) {
    padding-left: 132px;
  }
}
.header__nav-link {
  width: 100%;
  display: inline-block;
  text-align: center;
  font-family: "PT Sans", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 40px;
  text-transform: uppercase;
