.page-form {
  &__title {
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 26px;
    text-align: center;
    text-transform: uppercase;
    color: $black;
    margin-bottom: 28px;

    @media (min-width: $tablet-width) {
      font-size: 30px;
      margin-bottom: 33px;
    }
  }

  &__text {
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    text-transform: uppercase;
    color: $dark-grey;
    text-align: center;
  }

  &__header {
    padding: 37px 0 18px 0;
    width: 320px;
    margin: 0 auto;

    @media (min-width: $tablet-width) {
      padding: 70px 0 62px 0;
      width: 444px;
    }

    @media (min-width: $desktop-width) {
      padding: 109px 0 57px 0;
    }
  }
}
