.promo {
  &__wrapper {
    background-image: url("../image/background/promo__back.webp");
    background-repeat: no-repeat;
    background-size: auto 389px;
    background-position: center;

    @media (min-resolution: 2dppx) {
      background-image: url("../image/background/promo__back@2x.webp");
      background-size: auto 389px;
    }

    @media (min-width: $tablet-width) {
      background-size: 1200px 532px;
      background-position: center -14px;

      @media (min-resolution: 2dppx) {
        background-size: 1200px 532px;
      }
    }
  }

  &__wrapper-second {
    background-image: url("../image/background/promo__back.webp");
    background-repeat: no-repeat;
    background-size: auto 389px;
    background-position: center -231px;

    @media (min-resolution: 2dppx) {
      background-image: url("../image/background/promo__back@2x.webp");
      background-size: auto 389px;
    }

    @media (min-width: $tablet-width) {
      background-size: 1200px 521px;

      @media (min-resolution: 2dppx) {
        background-size: 1200px 521px;
      }
    }

    @media (min-width: $desktop-width) {
      background-size: 1200px 533px;

      @media (min-resolution: 2dppx) {
        background-size: 1200px 533px;
      }
    }
  }

  &__image {
    padding: 90px 19px 85px 19px;
    margin: 0 auto;
    display: block;

    @media (min-width: $tablet-width) {
      padding: 77px 19px 91px 19px;
    }
  }

  &__image-second {
    margin: 0 auto;
    display: block;
    padding: 52px 0 67px 0;

    @media (min-width: $tablet-width) {
      padding: 95px 0 117px 0;
    }

    @media (min-width: $desktop-width) {
      padding: 101px 0 123px 0;
    }
  }
}
