.modal-error {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: none;

  &__wrapper {
    width: 320px;
    background-color: #ffffff;
    box-shadow: 0 7px 15px rgba(0, 1, 1, 0.15);
    border-radius: 1px;
    position: relative;
    margin: auto;

    @media (min-width: $tablet-width) {
      width: 400px;
    }
  }

  &__content {
    padding: 40px 42px 35px 41px;
  }

  &__title {
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 26px;
    text-align: center;
    text-transform: uppercase;
    color: $black;
    margin-bottom: 30px;

    @media (min-width: $tablet-width) {
      font-size: 30px;
      line-height: 40px;
      padding: 0 35px;
    }
  }

  &__text {
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    text-align: center;
    text-transform: uppercase;
    color: $dark-grey;
    margin-bottom: 22px;
  }

  &__button {
    display: block;
    margin: 0 auto;
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 26px;
    text-transform: uppercase;
    color: #ffffff;
    border: none;
    background-color: $light-blue;
    padding: 18px 0 14px 0;
    min-width: 180px;

    @media (min-width: $tablet-width) {
      min-width: 272px;
    }
  }

  &--open {
    display: flex;
  }
}
