.search {
  &__wrapper {
    padding: 33px 20px 49px;
    margin: 0 auto;
    width: 320px;

    @media (min-width: $tablet-width) {
      padding: 57px 0 58px;
      min-width: 458px;
    }

    @media (min-width: $desktop-width) {
      padding: 57px 0 66px;
    }
  }

  &__heading {
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 21px;
    line-height: 26px;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 22px;

    @media (min-width: $tablet-width) {
      font-size: 30px;
      margin-bottom: 34px;
    }
  }

  &__text {
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    text-align: center;
    text-transform: uppercase;
    color: $dark-grey;
    margin-bottom: 27px;

    br {
      display: none;
    }

    @media (min-width: $tablet-width) {
      margin-bottom: 37px;

      br {
        display: block;
      }
    }

    @media (min-width: $desktop-width) {
      margin-bottom: 43px;
    }
  }

  &__button {
    background-color: $light-blue;
    border: none;
    width: 100%;
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 21px;
    line-height: 26px;
    text-transform: uppercase;
    color: #ffffff;
    padding: 16px 0;

    &:hover {
      background-color: $medium-dark-blue;
    }

    &:active {
      background-color: $dark-blue;
      color: rgba(255, 255, 255, 0.3);
    }

    &:disabled {
      background-color: #e5e5e5;
    }
  }
}
