.card-list {
  list-style-type: none;

  &__group-title {
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 26px;
    text-align: center;
    text-transform: uppercase;
    color: #ffffff;
    margin-bottom: 12px;

    br {
      display: none;
    }

    @media (min-width: $tablet-width) {
      font-size: 21px;
      padding: 0 17px;
    }

    @media (min-width: $desktop-width) {
      br {
        display: block;
      }
    }
  }

  &__group-number,
  &__group-text {
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    text-align: center;
    text-transform: uppercase;
    color: #ffffff;
    display: block;
  }

  &__group-number {
    margin-bottom: 16px;

    @media (min-width: $tablet-width) {
      margin-bottom: 24px;
    }
  }

  &__group {
    background-color: $light-blue;

    @media (min-width: $tablet-width) {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-column: 1/-1;
    }
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    width: 320px;
    margin: 0 auto;
    position: relative;

    @media (min-width: $tablet-width) {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      width: 768px;
    }

    @media (min-width: $desktop-width) {
      width: 1200px;
    }

    &--corner::before {
      position: absolute;
      content: "";
      background-image: url("../image/background/corner__advantage.svg");
      background-repeat: no-repeat;
      background-position: top center;
      width: 100vw;
      height: 37px;
      top: -16px;
      right: 0;
      left: 50%;
      transform: translateX(-50%);
      bottom: 0;

      @media (min-width: $tablet-width) {
        content: none;
      }
    }
  }

  &__image {
    display: block;
    object-fit: cover;
    height: auto;
    max-width: 100%;
    min-height: 100%;
  }

  &__group-item {
    &--paddingfisrt {
      padding: 55px 20px 39px 18px;

      @media (min-width: $tablet-width) {
        padding: 40px 32px 43px;
      }

      @media (min-width: $desktop-width) {
        padding: 47px 55px 56px;
      }
    }

    &--paddingsecond {
      padding: 39px 16px 35px 15px;

      @media (min-width: $tablet-width) {
        padding: 43px 36px 41px;
      }

      @media (min-width: $desktop-width) {
        padding: 51px 83px 53px;
      }
    }
  }

  &__inner-list {
    list-style-type: none;

    @media (min-width: $tablet-width) {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-column: 1/-1;
    }
  }

  &__inner-title {
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 26px;
    text-transform: uppercase;
    color: $black;
    margin-bottom: 17px;

    @media (min-width: $tablet-width) {
      margin-bottom: 23px;
      font-size: 21px;
    }
  }

  &__inner-text {
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    align-items: center;
    text-transform: uppercase;
    color: $dark-grey;
  }

  &__inner-item {
    background-repeat: no-repeat;
    padding: 36px 11px 0 69px;

    @media (min-width: $tablet-width) {
      text-align: center;
      background-position: 50% 0;
    }

    &:last-child {
      padding-bottom: 34px;

      @media (min-width: $tablet-width) {
        padding-bottom: 59px;
      }
    }

    &--house {
      background-image: url("../image/icons/icon-house.svg");
      background-position: 20px 26px;

      @media (min-width: $tablet-width) {
        padding: 158px 35px 59px 36px;
        background-image: url("../image/icons/icon-house__lg.svg");
        background-position: 50% 60px;
      }

      @media (min-width: $desktop-width) {
        padding: 158px 46px 80px;
      }
    }

    &--souvenirs {
      background-image: url("../image/icons/icon-gift.svg");
      background-position: 24px 14px;
      padding: 25px 11px 0 69px;

      @media (min-width: $mobile-width) {
        br {
          display: none;
        }
      }

      @media (min-width: $tablet-width) {
        padding: 158px 31px 81px 32px;
        order: 1;
        background-image: url("../image/icons/icon-gift__lg.svg");
        background-position: 50% 57px;
      }

      @media (min-width: $desktop-width) {
        padding: 158px 78px 81px;

        br {
          display: block;
        }
      }
    }

    &--food {
      background-image: url("../image/icons/icon-burger.svg");
      background-position: 19px 17px;
      padding: 24px 11px 0 69px;

      @media (min-width: $tablet-width) {
        padding: 158px 50px 59px;
        background-image: url("../image/icons/icon-burger__lg.svg");
        background-position: 50% 63px;
      }

      @media (min-width: $desktop-width) {
        padding: 157px 49px 81px;

        br {
          display: none;
        }
      }
    }
  }

  &__title {
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 26px;
    text-align: center;
    text-transform: uppercase;
    color: $black;
    margin-bottom: 13px;

    br {
      display: none;
    }

    @media (min-width: $tablet-width) {
      font-size: 21px;

      br {
        display: block;
      }
    }
  }

  &__counter-number,
  &__text {
    font-family: "PT Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    text-transform: uppercase;
    color: $dark-grey;
    display: block;
    text-align: center;

    br {
      display: none;
    }

    @media (min-width: $tablet-width) {
      br {
        display: block;
      }
    }

    @media (min-width: $desktop-width) {
      br:nth-child(1) {
        display: none;
      }
    }
  }

  &__counter-number {
    margin-bottom: 17px;

    @media (min-width: $tablet-width) {
      margin-bottom: 23px;
    }
  }

  &__item {
    background-color: $grey;
    padding: 26px 32px 22px 32px;
    position: relative;

    @media (min-width: $tablet-width) {
      padding: 42px 34px 41px;
    }

    @media (min-width: $desktop-width) {
      padding: 42px 43px 62px;
    }

    &:last-child {
      padding-bottom: 24px;
    }

    &--border-bottom::before {
      content: "";
      width: 280px;
      height: 1px;
      position: absolute;
      bottom: 0;
      right: 18px;
      background-color: #d6d6d6;

      @media (min-width: $tablet-width) {
        content: none;
      }
    }

    &:last-child::before {
      content: none;
    }
  }

  &__wrapper-picture {
    @media (min-width: $tablet-width) {
      grid-column: 2/-1;
      grid-row: 1/2;

      &--second {
        grid-column: 1/3;
      }
    }
  }
}
